import { normalize } from '@ensdomains/eth-ens-namehash'

import { emptyAddress } from '../../utils/utils'
import getFNS from '../../apollo/mutations/ens'
import { isFNSReadyReactive } from '../../apollo/reactiveVars'

export default async (_, { address }) => {
  let name = emptyAddress
  const fns = getFNS()
  const obj = {
    name,
    address,
    avatar: '',
    match: false,
    __typename: 'ReverseRecord'
  }
  if (!address || !isFNSReadyReactive()) return obj

  try {
    const { name: reverseName } = await fns.getName(address)
    const reverseAddress = await fns.getAddress(reverseName)
    const normalisedName = normalize(reverseName)
    if (
      parseInt(address) === parseInt(reverseAddress) &&
      reverseName === normalisedName
    ) {
      name = reverseName
    }
    if (name !== null) {
      const avatar = await fns.getText(name, 'avatar')
      return {
        ...obj,
        name,
        addr: reverseAddress,
        avatar,
        match: false
      }
    } else {
      return {
        ...obj,
        name: null,
        match: false
      }
    }
  } catch (e) {
    console.log(e)
    return {
      ...obj,
      name: null,
      match: false
    }
  }
}
